body,
html,
#root,
.rs-container {
  height: 100%;
}

/* TODO */
body {
  overflow: hidden;
  border-top: 1px solid var(--rs-divider-border);
}

.react-contextmenu {
  z-index: 10;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.flex-auto {
  flex: auto;
}

.flex-block {
  display: flex;
}

.flex-align-center {
  align-items: center;
}

.full-height {
  height: 100%;
}

.full-width {
  width: 100%;
}

.p-10 {
  padding: 10px;
}

.b-top {
  border-top: 1px solid var(--rs-divider-border);
}

.delete-button,
.delete-button:active,
.delete-button:focus,
.delete-button:hover {
  color: var(--rs-red-600);
}

.align-center {
  text-align: center;
}

.align-right {
  text-align: right;
}

.centered-span {
  text-align: center;
  display: inline-block;
  vertical-align: middle;
}

.reset-margin {
  margin: 0;
}

.hover-icon:hover {
  color: var(--rs-primary-700);
}

::-webkit-scrollbar {
  border-radius: 5px;
  width: 6px;
}

::-webkit-scrollbar-thumb {
  background-color: hsla(0, 0%, 55%, 0.3);
  border-radius: 5px;
}

::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 5px;
}
